  background-color: rgb(30, 46, 77);
  position: relative;
}
body main {
  min-height: 100vh;
  padding: 1.875rem;
}

h1 {
  font-size: 2rem;
  color: white;
  font-family: "Pacifico", cursive;
  text-shadow: 0.0625rem 0.156rem 0.156rem rgb(88, 85, 85);
}

button {
  font-size: 1rem;
  font-weight: 600;
  color: white;
  background-color: rgb(30, 46, 77);
  border: none;
}

button:hover {
  background-color: rgba(30, 46, 77, 0.589);
  cursor: pointer;
}

@keyframes fade-slide-in {
  from {
    transform: translateY(-5rem);
  }
  to {
    transform: translateY(0);
  }
}
.flex-col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.flex-col__gap15 {
  gap: 0.9375rem;
}
.flex-col__gap20 {
  gap: 1.25rem;
}
.flex-col__gap50 {
  gap: 5.625rem;
}
.flex-col__gap70 {
  gap: 4.375rem;
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.flex-row__gap10 {
  gap: 10px;
}

.white-patch {
  width: 100%;
  max-width: 31.25rem;
  border-radius: 1.875rem;
  background-color: white;
  padding: 1.875rem;
}
.white-patch .search-field {
