  font-weight: 500;
}
.note-widget__date {
  font-size: 0.875rem;
  color: rgb(30, 46, 77);
  font-weight: 500;
}

.save-button {
  height: 3.125rem;
  width: 5rem;
  border-radius: 1.125rem;
  font-size: 0.875rem;
  outline: none;
}

.writing-area {
  min-height: 90%;
  min-width: 85%;
  margin-top: 1.375rem;
}
.writing-area__title {
  width: 100%;
