  color: rgba(247, 58, 58, 0.911);
}
.alert-box__button {
  width: 3.75rem;
  height: 2.8125rem;
  border-radius: 3.125rem;
  background-color: rgb(30, 46, 77);
  color: white;
  font-size: 0.875rem;
  font-weight: 600;
